#area-desc {
  grid-area:desc;
  padding: 20px;
}
#area-list {
  grid-area:list;
  overflow: auto; 
  text-align: center;
  display: block;
  height: calc(100vh - 3rem);
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
#grid-gallery {
  display: grid;
  grid-template-columns: 300px auto;
  grid-template-rows: 3rem auto;
  grid-template-areas: 
  ". ."
  "list desc";
  background-color: #DDDDDD;
  color: #333333;
}
.gallery-item {
  float: center;
  padding: 20px;
}
.gallery-item img {
  height: auto;
  width: 90%;
}

@media screen and (max-width: 767px){
  #area-list {
    height: 250px;
    grid-area:list;
    padding:0;
    margin:0;
    white-space:nowrap;
    overflow:auto;
  }
  #grid-gallery {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 3rem auto auto;
    grid-template-areas:
    "."
    "list"
    "desc";
  }
  .gallery-item {
    height:80%;
    float: none;
    padding: 20px;
    display:inline-block;
    *zoom:1;/*IE*/
    white-space:normal;
  }
  .gallery-item img{
    width: auto;
    height: 90%;
  }
}



