#area-name { 
  padding-top: 20px;
  grid-area: name;
}
#area-quotes { 
  padding-top: 50px;
  padding-left: 20px;
  padding-right: 20px;
  grid-area: quotes;
}
#area-face { grid-area: face; }
#area-intro { 
  grid-area: intro; 
  padding: 20px;
}
#grid-home {
  display: grid;
  grid-template-columns: 1fr 2fr;
  grid-template-rows: 3rem auto auto auto auto;
  grid-template-areas: 
  ". ."
  "name quotes"
  "name intro"
  "face intro"
  "face intro";
  min-height: calc(100vh);
  background-color: #DDDDDD;
  color: #333333;
}
@media screen and (max-width: 767px){
  #grid-home {
    grid-template-columns: 1fr;
    grid-template-rows: 3rem auto auto auto auto;
    grid-template-areas: 
    "."
    "name"
    "face"
    "quotes"
    "intro";
  }
  #area-quotes { 
    text-align: center;
  }
}

.my-name{
  line-height: 8vw;
  text-align: center;
  font-size : 10vmax;
  margin-bottom : 10%;
  display: block;
}

.profilePic{
  text-align: center;
  padding-bottom: 20px;
  
}
.profilePic img{
  -webkit-animation: zoom 2s ease;
          animation: zoom 2s ease;
  height: 16vw;
}
.profilePic img:hover{
  -webkit-animation: spin-head infinite 2s linear;
          animation: spin-head infinite 2s linear;
  height: 16vw;
}
@-webkit-keyframes spin-head {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}
@keyframes spin-head {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}
@-webkit-keyframes zoom {
  from {opacity:0}
  to {opacity:1}
}
@keyframes zoom {
  from {opacity:0}
  to {opacity:1}
}


