#area-title { 
  grid-area: title;
  text-align: center;
  margin-bottom: -100px;
}
#area-phone { grid-area: phone; }
#area-email { grid-area: email; }
#area-linkedin { grid-area: linkedin; }
#area-github { grid-area: github; }
#area-result { grid-area: result; }
#grid-contact{
  display: grid;
  grid-template-columns: auto auto auto auto;
  grid-template-rows: 3rem auto auto auto;
  grid-template-areas: 
  ". . . ."
  "title title title title"
  "phone email linkedin github"
  "result result result result";

  background-color: #DDDDDD;
  color: #333333;
  min-height: 100vh;
}

#area-phone,#area-email,#area-linkedin,#area-github {
  text-align: center;
  height: 140px;
}

.contact-icons{
  height:100px;
  width:100px;
  margin-top: 20px;
}


@media screen and (max-width: 767px){
  #area-title { 
    margin-bottom:0px;
    /* background: purple; */
  }
  #grid-contact{
    display: grid;
    grid-template-columns: auto auto;
    grid-template-rows: auto auto auto auto auto;
    grid-template-areas: 
    "header header"
    "title title"
    "phone email"
    "linkedin github"
    "result result ";
  }
  #area-phone img,#area-email img,#area-linkedin img,#area-github img{
    height:70px;
    width:70px;
  }
}

#area-phone:hover,#area-email:hover,#area-linkedin:hover,#area-github:hover {
  background-color: #CCCCCC
}

#area-result{
  text-align: center;
}



