#to-the-top{
  z-index: 9999999999999999;
}

div.menu-btn{
  margin: 0;
  position:fixed;
  top:0;
  width:100%;
  background-color: #333;
  display: none;
}

div.myNav{
  padding: 0;
  background-color: #333;
  position:fixed;
  top:0;
  width:100%;
}

.myNav ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
  overflow: hidden;
}

.myNav li {
  float: left;
  text-align: center;
  padding-left: 1rem;
  padding-right: 1rem;
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
  display: block;
  font-size: 2rem;
}

.myNav li a {
  color: white;
  text-decoration: none;
}

/* Change the link color to #111 (black) on hover */
.myNav li:hover{
  /*height: 100%;*/
  background-color: #111;
}
.menu-btn img{
  display : none;
  float:right;
  padding-right: 1rem;
  height: 3rem;
  width: 3rem;
}

@media screen and (min-width: 600px){
  
}

@media screen and (max-width: 600px){
  div.menu-btn{
      display: block;
      top:0;
  }
  .menu-btn img{
      display: block;
  }
  div.myNav{
      display: none;
      top:3rem;

  }

.myNav li{
  float:none;
      text-align: center;
      display: block;
}

  .myNav img{
      display:block;
  }
  .myNav li:hover{
  /*height: 100%;*/
  background-color: #111;
}
}


